<template>
  <div id="art" @touchstart="hideList">
    <div id="main" :style="news.bg">
      <p>{{news.title}}</p>
    </div>
    <div class="art-content">
      <div class="basic-info">
        <p class="left">{{news.news_date}}</p>
        <a :href="news.news_url" target="_blank" class="left">
          <p>{{news.news_source}}</p>
        </a>
      </div>
      <div class="art-content-detail">
        <p v-for="(item, index) in news.news_content" v-bind:key="index">{{item}}</p>
        <!--<p>{{news.news_content}}</p>-->
      </div>
      <div class="recommendation">
        <p v-for="(item, index) in recommendation" v-bind:key="index" class="recommendation-item" :class="{'pointer': index !== 0}">
          {{item}}
        </p>
      </div>
    </div>
    <foot></foot>
  </div>
</template>

<script>
import $ from 'jquery'
import foot from './Foot'
export default {
  name: 'art',
  data () {
    return {
      news: {
        id: '',
        title: '',
        bg: '',
        news_date: '',
        news_source: '',
        news_url: '',
        news_content: ''
      },
      recommendation: [
        '推荐新闻',
        '延布工业城 石油王国的智慧蓝图',
        'ABB：互联世界中的智能制造',
        '华为携手石化盈科打造智能工厂2.0',
        '破解东风本田网络管理难题，华为CloudCampus有一套',
        '德国盖尔森基兴：小城里的大智慧'
      ]
    }
  },
  methods: {
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    }
  },
  components: {
    foot
  },
  created () {
    var self = this
    console.log(self.$route.query.articleId)
    $.ajax({
      async: false,
      url: '/news/',
      type: 'GET',
      data: {news_id: self.$route.query.articleId},
      success: function (data) {
        self.news = {
          id: data.news_id,
          title: data.title.indexOf('≮') === -1 ? data.title : data.title.split('≮')[1],
          bg: data.bg,
          news_date: data.news_date,
          news_source: data.news_source,
          news_url: data.news_url,
          news_content: data.news_content.split('≮')
        }
      }
    })
  }
}
</script>

<style scoped>
  #main{
    background-size: cover;
    overflow: hidden;
    position: relative;
    background-size: cover!important;
  }
  #main p{
    color: white;
    text-align: center;
  }
  .art-content{
    overflow: hidden;
  }
  .basic-info{
    width: 100%;
    overflow: hidden;
  }
  .basic-info a{
    text-decoration: none;
    color: black;
  }
  .art-content-detail{
    width: 100%;
    height: auto;
  }
  .recommendation{
    width: 100%;
    height: auto;
  }
  .recommendation-item{
    width: 100%;
    height: auto;
    font-size: 18px;
    margin: 8px 0;
    padding: 6px 0;
  }

  @media only screen and (max-device-width: 767px) {
    #main {
      width: 100%;
      height: 350px;
    }
    #main p{
      font-size: 24px;
      line-height: 120%;
      margin-top: 150px;
      padding: 0 90px;
    }
    .art-content{
      width: calc(100% - 40px);
      margin: 0 20px;
      height: auto;
      padding: 16px 0;
    }
    .basic-info{
      height: 26px;
      border-bottom: 2px solid #f0f2f5;
    }
    .basic-info p{
      font-size: 16px;
      margin-right: 12px;
    }
    .art-content-detail{
      margin-top: 0;
    }
    .art-content-detail p{
      font-size: 18px;
      line-height: 180%;
      padding: 6px 0;
    }
    .recommendation{
      margin-top: 6px;
    }
    .recommendation-item{
      line-height: 120%;
    }
    .recommendation-item:first-of-type{
      border-bottom: 2px solid #f0f2f5;
      font-weight: bold;
      margin-top: 0;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #main p{
      font-size: 36px;
      line-height: 120%;
      padding: 0 90px;
      margin-top: 280px;
    }
    .art-content{
      width: 426px;
      margin: 0 calc((100% - 426px) / 2);
      height: auto;
      padding: 30px 0;
    }
    .basic-info{
      height: 30px;
      border-bottom: 1px solid #f0f2f5;
    }
    .basic-info p{
      font-size: 16px;
      margin-right: 12px;
    }
    .art-content-detail{
      margin-top: 6px;
    }
    .art-content-detail p{
      font-size: 18px;
      line-height: 180%;
      padding: 10px 0;
    }
    .recommendation{
      margin-top: 18px;
    }
    .recommendation-item{
      line-height: 160%;
    }
    .recommendation-item:first-of-type{
      font-weight: bold;
      border-bottom: 1px solid #f0f2f5;
      margin-top: 0;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 668px) {
    .art-content{
      width: 640px;
      margin: 0 calc((100% - 640px) / 2);
    }
    .art-content-detail, .recommendation{
      width: 100%;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 878px) {
    .art-content{
      width: 850px;
      margin: 0 calc((100% - 850px) / 2);
    }
    .art-content-detail, .recommendation{
      width: 100%;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1094px) {
    .art-content{
      width: 1066px;
      margin: 0 calc((100% - 1066px) / 2);
    }
    .art-content-detail{
      width: 746px;
      padding-right: 40px;
      float: left;
    }
    .recommendation{
      width: calc(100% - 786px);
      float: right;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 1308px) {
    .art-content{
      width: 1280px;
      margin: 0 calc((100% - 1280px) / 2);
    }
    .art-content-detail{
      width: 960px;
      padding-right: 40px;
    }
    .recommendation{
      width: calc(100% - 1000px);
    }
  }
</style>



